<template>
  <el-dialog
    class="m-orderManage-auditDialog"
    :visible="visible"
    title="风控总监审核"
    append-to-body
    width="900px"
    @close="closeEvent">
    <el-row v-if="target.wind_control_leader.status === -1" style="padding: 10px 20px;border:2px red dotted;margin-bottom:20px;">
      驳回理由：{{ target.bill_info.reject }}
    </el-row>
    <div class="detail-title" style="border-bottom:none;">客户：<el-button style="font-size:16px;" @click.native.prevent="onCheckEvent" type="text">{{ target.customer.name }}</el-button></div>
    <el-form :model="item" :rules="rules" ref="itemForm" label-position="right" label-width="120px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="最终额度（元）" prop="money">
            <el-col :span="14">
              <el-input-number v-model="item.money" controls-position="right"></el-input-number>
            </el-col>
          </el-form-item>
        </el-col>
        <!-- <el-col :span="12">
          <el-form-item label="合同模版" prop="contract_id">
            <el-select v-model="item.contract_id" placeholder="请选择">
              <el-option v-for="contract in contracts" :key="contract.id" :label="contract.name" :value="contract.id"></el-option>
            </el-select>
          </el-form-item>
        </el-col> -->
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="审核意见">
            <el-input :style="{width: '300px'}" v-model="item.opinion" type="textarea" placeholder="请输入描述" :rows="4" />
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="onPassEvent">通 过</el-button>
      <el-button @click="visible = false">取 消</el-button>
    </div>
  </el-dialog>
</template>
<script>
export default {
  props:['target'],
  data(){
    return{
      visible: true,
      item: {},
      rules: {
        money:[
          { type: 'number', required: true, message: '请填写最终额度', trigger: 'blur' }
        ],
        contract_id:[
          { type: 'number', required: true, message: '请选择合同模版', trigger: 'change' }
        ]
      },
      contracts:[
        {name: '普通合同', id: 1},
        {name: '一般合同', id: 2}
      ]
    }
  },
  computed:{
    info(){
      return this.item.wind_control;
    },
    total_score(){
      let total = 0;
      for(let key in this.item.score_card){
        total += this.item.score_card[key];
      }
      return total;
    }
  },
  methods:{
    onCheckEvent(){
      this.$emit('detail');
    },
    closeEvent(){
      const that = this;
      this.visible = false;
      setTimeout(()=>{that.$emit('close');},500);
    },
    onPassEvent(){
      this.$refs['itemForm'].validate((valid) => {
        if(valid){
          this.$emit('pass', this.item);
        }else{
          this.$message('请正确填写表单！');
        }
      });
    }
  },
  mounted(){
    if(!this.target.wind_control_leader.wind_control_leader_id){
      this.item = {   
        "bill_id": this.target.bill_info.bill_id,                //订单id 
        "bill_status": 3,           //审核之后的订单状态 (驳回到之前的任意一个审核状态，或者下个状态)
        "money": 0,               //最终额度
        "contract_id": 1,           //合同模版di
        "opinion": "没意见"         //审核意见
      }
    }else{
      this.item = this.target.wind_control_leader;
      this.item.bill_id = this.target.bill_info.bill_id;
      this.item.bill_status = 3;
    }
  }
}
</script>
<style scoped>
  .m-orderManage-auditDialog .detail-title {
    line-height: 24px;
    font-size: 16px;
    color: #2d2f33;
    border-bottom: 1px #ddd solid;
    margin-bottom: 10px;
    padding: 0 30px;
  }
  .m-orderManage-auditDialog .el-form {
    padding: 20px;
  }
  .m-orderManage-auditDialog .score_card_panel{
    border: 2px #ddd dotted;
    margin-bottom: 20px;
  }
  .m-orderManage-auditDialog .score_card_panel .title{
    padding: 20px;
    font-size: 16px;
    font-weight: bold;
  }
</style>




