<style>
  .m-orderManage-windCtrlAudit{
    padding: 20px;
  }
</style>

<template>
  <div class="m-orderManage-windCtrlAudit">
    <order-table 
      ref="table"
      stage="2"
      @detail="onCheckEvent"
      @audit="onAuditEvent"
      @reject="onRejectEvent"/>
    <!-- 审核弹框 -->
    <audit-dialog 
      v-if="auditDialog"
      :target="item"
      @detail="showDetailInAudit"
      @close="auditDialog = false"
      @pass="onPass"/>
    <!-- 订单详情 -->
    <detail-dialog
      v-if="detailDialog"
      :item="item"
      :stage="2"
      @close="detailDialog = false"/>
  </div>
</template>
<script>
import { rest } from '@/common/request';
import AuditByCEODialog from './common/auditByCEODialog.vue';
import OrderTable from './common/orderTable.vue';
import DetailDialog from './common/detailDialog.vue';

export default {
  components:{
    'audit-dialog': AuditByCEODialog,
    'order-table': OrderTable,
    'detail-dialog': DetailDialog
  },
  data(){
    return {
      item: {
        bill_info:{},
        car:{
          car_regist:[],
          drive_license:[],
          policy:[],
          evaluation_form:[],
          key_picture:[],
          car_picture:[],
          car_video:[]
        },
        customer:{
          id_card_picture:[],
          apply_form:[]
        },
        finance:{},
        guarantor:{
          guarantor_id_card_picture:[]
        },
        sign:{},
        user_info:{},
        wind_control:{},
        wind_control_leader:{}
      },
      detailDialog: false,
      auditDialog: false
    }
  },
  methods:{
    getOrderItem(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/bill/detail',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'GET',
        params: data
      }).then(res => {
        this.item = res.data.data;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    onCheckEvent(row){
      this.getOrderItem({bill_id: row.bill_id}).then(()=>{
        this.detailDialog = true;
      })
    },
    onAuditEvent(row){
      this.getOrderItem({bill_id: row.bill_id}).then(()=>{
        this.auditDialog = true;
      })
    },
    onRejectEvent(row){
      let statusDict = {
        '审核驳回':-1,
        '草稿':0,
        '风控专员审核中':1,
        '风控总监审核中':2,
        '客服专员签约中':3,
        '财务审批中':4
      }
      this.$prompt(`驳回（订单号:${row.bill_id}）的理由`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputType: 'textarea',
          inputPattern: /\S/,
          inputErrorMessage: '请输入驳回理由'
        }).then(({ value }) => {
          let uid = sessionStorage.getItem('ts_user_id');
          return rest({
            url: '/v1/inspect/'+ uid +'/wind_control/leader',
            headers:{
              'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
            },
            method: 'POST',
            data: {
              bill_id: row.bill_id,
              reject: value,
              bill_status: 1,
              status: -1
            }
          }).then(res => {
            this.$message({
              message: '驳回成功！',
              type: 'success'
            });
            this.$refs.table.reloadList();
          });
        }).catch(() => {
           
        });
    },
    showDetailInAudit(){
      this.detailDialog = true;
    },
    onPass(item){
      item.status = 1;
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/wind_control/leader',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: item
      }).then(res => {
        this.$message({
          message: '审核通过成功！',
          type: 'success'
        });
        this.auditDialog = false;
        this.$refs.table.reloadList();
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    }
  }
}
</script>




